let intervalRepeat = null

const awaitLoading = (element, selector, action) => new Promise(resolve => {
  const targetNode = element
  const config = { attributes: false, childList: true, subtree: true }
  let state = 0
  let repeat = 0

  const observer = new MutationObserver((mutationsList) => {
    for (const mutation of mutationsList) {
      mutationChange(mutation, observer, selector, state, resolve)
    }
  })

  intervalRepeat = setInterval(() => {
    repeat += 1

    if (state === 2) {
      complete(observer, resolve)
    }

    state = 0
    repeated(action, repeat, observer, resolve)
  }, 3000)

  observer.observe(targetNode, config)
  action()
})

const mutationChange = (mutation, observer, selector, state, resolve) => {
  if (mutation.type !== 'childList') {
    return false
  }

  state = getState(mutation, selector, state)
  if (state === 2) {
    complete(observer, resolve)
  }
}

const getState = (mutation, selector, state) => {
  const addedClassNameList = Array.from(mutation.addedNodes).map(el => el.className)
  const removedClassNameList = Array.from(mutation.removedNodes).map(el => el.className)

  const removeFrete = 'cart_totals calculated_shipping processing'

  if (addedClassNameList.includes(selector)) return 1
  if (removedClassNameList.includes(selector)) return 2
  if (removedClassNameList.includes(removeFrete)) return 2

  return state
}

const complete = (observer, resolve) => {
  console.log('awaitLoadingBlockUI complete')
  clearInterval(intervalRepeat)
  observer.disconnect()
  return resolve()
}

const repeated = (action, repeat, observer, resolve) => {
  if (repeat === 3) {
    return complete(observer, resolve)
  }
  return action()
}

const awaitLoadingBlockUI = async (action) => {
  return awaitLoading(
    document.querySelector('.woocommerce'),
    'blockUI',
    action
  )
}

export default awaitLoadingBlockUI
